export default "import React, { useRef } from \"react\";\nimport Table from \"af-virtual-scroll/lib/Table\";\nimport { css } from \"@emotion/core\";\n\nconst wrapperCss = css`\n    display: flex;\n    flex-flow: column nowrap;\n`;\n\nconst columns = [\n    {\n        dataKey: \"a\",\n        label: \"a\"\n    },\n    {\n        dataKey: \"b\",\n        label: \"b\"\n    },\n    {\n        dataKey: \"c\",\n        label: \"c\"\n    }\n];\n\nconst getRowData = index => ({\n    a: index,\n    b: `cell_b_row: ${index}`,\n    c: `cell_c_row: ${index}`\n})\n\nconst TableWithScrollToRowButton = ({ className }) => {\n\n    const dataRef = useRef();\n\n    const submitHandler = e => {\n        e.preventDefault();\n        const v = e.currentTarget.elements.scrollRow.value;\n        dataRef.current.scrollToRow( +v );\n    };\n\n    return (\n        <div css={wrapperCss} className={className}>\n            <form onSubmit={submitHandler}>\n                <label>\n                    Row:&nbsp;\n                    <input\n                        name=\"scrollRow\"\n                        type=\"number\"\n                        defaultValue=\"0\"\n                    />\n                </label>\n                <button type=\"submit\">\n                    Scroll\n                </button>\n            </form>\n            <Table\n                dataRef={dataRef}\n                getRowData={getRowData}\n                rowCount={50000}\n                columns={columns}\n            />\n        </div>\n    );\n};\n\nexport default TableWithScrollToRowButton;";