export default "import React from \"react\";\nimport Table from \"af-virtual-scroll/lib/Table\";\n\nconst columns = [\n    {\n        dataKey: \"a\",\n        label: \"a\"\n    },\n    {\n        dataKey: \"b\",\n        label: \"b\"\n    },\n    {\n        dataKey: \"c\",\n        label: \"c\"\n    }\n];\n\nconst getRowData = index => ({\n    a: index,\n    b: `cell_b_row: ${index}`,\n    c: `cell_c_row: ${index}`\n})\n\nconst SimpleTable = ({ className }) => (\n    <Table\n        className={className}\n        getRowData={getRowData}\n        rowCount={500}\n        columns={columns}\n    />\n);\n\nexport default SimpleTable;";