export default "import React from \"react\";\nimport List from \"af-virtual-scroll/lib/List\";\n\nconst getRowData = index => `row ${index}`;\n\nconst SimpleList = ({ className }) => (\n    <List\n        fixedSize\n        className={className}\n        getRowData={getRowData}\n        rowCount={500000}\n    />\n);\n\nexport default SimpleList;";