export default "import React from \"react\";\nimport Table from \"af-virtual-scroll/lib/Table\";\nimport r from \"lodash/random\";\nimport times from \"lodash/times\";\nimport faker from \"faker\";\n\nconst columns = [\n    {\n        dataKey: \"rowIndex\",\n        label: \"Row index\",\n        getCellData: ( rowData, i ) => i\n    },\n    {\n        dataKey: \"num\",\n        label: \"Numeric\",\n        sort: \"numeric\"\n    },\n    {\n        dataKey: \"str\",\n        label: \"String\",\n        sort: \"locale\"\n    },\n    {\n        dataKey: \"rect\",\n        label: \"Rectangle\",\n        render: cellData => (\n            <div style={{\n                lineHeight: `${cellData}px`,\n                background: `hsl(${r(0,360)},50%,50%)`\n            }}>\n                height: {cellData}px\n            </div>\n        )\n    }\n];\n\nconst rowCount = 100000;\n\nconst rows = times( rowCount, () => ({\n    num: r( 1, 20000 ),\n    str: faker.name.findName(),\n    rect: r( 50, 250 )\n}));\n\nconst getRowData = index => rows[ index ];\n\nconst totals = {\n    num: [ \"sum\", \"count\" ]\n};\n\nconst VariableRowHeightsTable = ({ className }) => (\n    <Table\n        className={className}\n        totals={totals}\n        getRowData={getRowData}\n        rowCount={rowCount}\n        columns={columns}\n    />\n);\n\nexport default VariableRowHeightsTable;";