export default "import React from \"react\";\nimport Table from \"af-virtual-scroll/lib/Table\";\nimport r from \"lodash/random\";\nimport times from \"lodash/times\";\n\nconst columns = [\n    { dataKey: \"n1\", label: \"N1\" },\n    { dataKey: \"n2\", label: \"N2\" },\n    { dataKey: \"n3\", label: \"N3\" },\n    { dataKey: \"n4\", label: \"N4\" },\n];\n\nconst rowCount = 5000;\n\nconst rows = times( rowCount, () => ({\n    n1: r( 1, 20000 ),\n    n2: r( 1, 20000 ),\n    n3: r( 1, 20000 ),\n    n4: r( 1, 20000 )\n}));\n\nconst totals = {\n    n1: [ \"count\" ],\n    n2: [ \"sum\", \"max\" ],\n    n3: [ \"average\", \"min\" ],\n    n4: [ \"count\", \"sum\", \"average\" ]\n};\n\nconst getRowData = index => rows[ index ];\n\nconst TableWithTotals = ({ className }) => (\n    <Table\n        className={className}\n        getRowData={getRowData}\n        rowCount={rowCount}\n        columns={columns}\n        totals={totals}\n    />\n);\n\nexport default TableWithTotals;";